.div,
.rectangle {
  position: absolute;
}
.rectangle {
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: var(--color-lightyellow);
}
.div {
  top: calc(50% - 40px);
  left: 11.46%;
  line-height: 24px;
  font-weight: 500;
}
.about,
.home {
  top: calc(50% - 12px);
  line-height: 24px;
}
.home {
  position: absolute;
  left: 0;
}
.about {
  left: 40%;
